@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@1,900&display=swap);
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background: white;
  box-sizing: border-box;
  display: flex;
  text-align: center;
  justify-content: center;
}
.navbar {
    /* background-color: darkorchid; */
    background-color: white;
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    padding: auto;
    z-index: 10;
}

.nav-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 10rem;
}

.navbar img {
    width: 100px;
    padding: 1rem;
}

.navlinks {
    display: flex;
    justify-content: space-around;
}

.navlinks Link{
    text-decoration: none;
}

.navlinks li{
    list-style-type: none;
    margin: 0 20px;
    color: rgb(95, 95, 95);
}

.navlinks li:hover{
    color: rgb(30, 30, 30);
}

@media only screen and (max-width: 800px) {

    /* .navbar {
        height: 50px;
    } */

    .nav-items {
        display: flex;
        margin: 0 5rem;
    }

    .navlinks {
        display: none;
    }
}
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 50px;
    margin-top: 5rem;
}

.types {
    color: grey;
}

.types-container {
   display: block;
   justify-content: center;
   align-items: center;
}

.stats {
    display: block;
    margin-top: 4rem;
}

.stats-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.card-container {
    display: block;
    margin: 1rem;
    width: 200px;
    height: auto;
    box-shadow: 0 2px 5px 2px grey;
    border-radius: 10px;
    transition: 0.2s ease-in-out;
    cursor: pointer;
}

.card-container:hover {
    transform: scale(1.05);
    box-shadow: 0 9px 7px grey;
    z-index: 0;
}

.card-container:active {
    transform: scale(0.98);
}

.card-header {
    border-radius: 10px 10px 0 0;
    background-color: white;
}

.img {
    border-radius: 10px 10px 0 0;
    width: auto;
    max-width: 200px;
    height: 200px;
}

.name {
    background-color: rgb(173, 173, 173);
    color:white;
    padding: 1px;
}

.detail {
    padding: 1rem;
    background-color: lightblue;
    border-radius: 0 0 10px 10px;
    /* font-family: 'Nunito', sans-serif; */
}

p {
    font-family: 'Nunito', sans-serif;
}
.detail-container {
    display: flex;
    justify-content: left;
    align-items: center;
    border: 1px solid white;
}

.detail-container .detail-logo{
    width: 30px;
    padding: 0 1rem;
}

.detail-container .detail-name{
    width: 30px;
    color: rgb(110, 110, 110);
}
.stats {
    display: block;
    margin-top: 4rem;
}

.stats-container {
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
}
.pokemons {
    display: flex;
    flex-direction: column;
    margin-top: 5rem;
    justify-content: center;
    align-items: center;
}

.pokemon-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 2rem;
}

a {
    text-decoration: none;
}

@media only screen and (max-width: 800px) {
    .pokemon-list {
        padding: 1rem;
    }
}
input {
    align-items: flex-start;
    background-color:white;
    height: 20px;
    padding: 10px;
    border: none;
    border-bottom: 2px solid rgb(95, 95, 95);
    transition: 0.3s ease-in;
    font-size: medium;
}

::-webkit-input-placeholder {
    color: rgb(95, 95, 95);
}

:-ms-input-placeholder {
    color: rgb(95, 95, 95);
}

::-ms-input-placeholder {
    color: rgb(95, 95, 95);
}

::placeholder {
    color: rgb(95, 95, 95);
}

input:focus {
    outline: none;
}

.submit-btn {
    color: rgb(95, 95, 95);
    padding: 10px;
    margin: 10px;
    background-color: white;
    border: 1px solid rgb(95, 95, 95);
    cursor: pointer;
    outline: none;
    font-size: medium;
}

.submit-btn:hover {
    color: rgb(30, 30, 30);
}
